<template>
  <div>
    <div v-if="mode === 'DESIGN'">
      <el-select class="max-fill" v-if="!expanding" size="medium" multiple v-model="_value" disabled :placeholder="placeholder"/>
      <el-checkbox-group v-else v-model="_value">
        <el-checkbox disabled v-for="(op, index) in optionsShow" :key="index" :label="op.key">{{op.value}}</el-checkbox>
      </el-checkbox-group>
    </div>
    <div v-else>
      <el-select class="max-fill" v-if="!expanding" v-model="_value" multiple size="medium"
      :clearable="!perm||perm==='E'" :disabled="perm&&perm==='R'"
      :placeholder="placeholder">
        <el-option v-for="(op, index) in optionsShow" :key="index" :value="op.key" :label="op.value"></el-option>
      </el-select>
      <el-checkbox-group v-else v-model="_value">
        <el-checkbox :disabled="perm&&perm==='R'" v-for="(op, index) in optionsShow" :key="index" :label="op.key">{{op.value}}</el-checkbox>
      </el-checkbox-group>
    </div>
  </div>
</template>

<script>
import componentMinxins from '../ComponentMinxins'
import {selectOptions} from "@/api/form";

export default {
  mixins: [componentMinxins],
  name: "MultipleSelect",
  components: {},
  props:{
    placeholder:{
      type: String,
      default: '请选择选项'
    },
    value:{
      type: Array,
      default: () => {
        return []
      }
    },
      optionsFromRemote: {
          type: Boolean,
          default: false
      },
      remoteUrl: {
          type: String,
          default: ''
      },
    expanding:{
      type: Boolean,
      default: false
    },

    options:{
      type: Array,
      default: () => {
        return []
      }
    },
    perm:{
      type: String,
      default: 'E'
    }
  },
  computed:{
      //计算属性绑定金额
      optionsShow(){
          if(this.optionsFromRemote&&this.remoteUrl.length>0) {
              return this.remoteOptions;
          }

          return this.options;
      },
  },
    mounted() {

        if(this.optionsFromRemote&&this.remoteUrl.length>0&&this.mode != 'DESIGN'){
            selectOptions({
                remoteUrl:this.remoteUrl

            }).then(res=>{
                console.log(res)
                var data = res.data;
                this.remoteOptions=data;
            })
        }
    },
  data() {
    return {
      checks:[],
        remoteOptions:[]

    }
  },
  methods: {}
}
</script>

<style scoped>

</style>
